import React from 'react';
import Slide from './Slide.js';

class LandingPage extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            currScroll: 0,
            scrollBlock: false
        }
    }
    handleScroll = (event) => { 
        if (!this.state.scrollBlock && event.deltaY > 0)
        {
            if (this.state.currScroll < 20) this.setState({currScroll: this.state.currScroll+1});
        }
    }

    componentDidMount() {
        window.addEventListener('wheel', this.handleScroll, true);
    }
    
    componentWillUnmount() {
        window.removeEventListener('wheel', this.handleScroll);
    }

    isHidden = (limit) => {return  this.state.currScroll > limit }

    render() {
        return(
            <div>
                <Slide hidden={this.isHidden(18)} direction="top" backgroundColor="#27586B" textColor="#022735" text="Genießen Sie ihren Aufenthalt."></Slide>
                <Slide hidden={this.isHidden(12)} direction="right" backgroundColor="#7D2A68" textColor="#3F002F" text="Auf dieser Seite finden Sie alles was sie über mich wissen müssen."></Slide>
                <Slide hidden={this.isHidden(6)} direction="left" backgroundColor="#AA4B39" textColor="#550D00" text="Hi, Ich bin Bruno."></Slide>
            </div>
        )
    }
}

export default LandingPage;