import React from 'react';
import './css/Slide.css';

const Slide = function(props) { 
    return(
        <div className={"Slide "+(props.hidden ? "hidden-"+props.direction : "shown")} style={{backgroundColor: props.backgroundColor, color: props.textColor}}>
            <p>{props.text}</p>
        </div>
    );
}

export default Slide;
